<template>
  <el-input
    v-model="text"
    v-bind="$attrs"
    v-on="$listeners"
    @input.native="changeValue">
    <!-- <template v-slot:suffix></template> -->
  </el-input>
</template>

<script>
import { debounce } from 'throttle-debounce';

export default {
  name: 'FromInputDebounce',
  props: {
    delay: {
      type: Number,
      default: 300,
    },
    value: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      text: '',
    };
  },
  watch: {
    value: {
      handler(val) {
        this.text = val;
      },
      immediate: true,
    },
  },
  methods: {
    // 延迟处理键盘输入校验
    changeValue: debounce(300, function defer() {
      this.$emit('change', this.text);
    }),
  },
  // render(_, { props, slots }) {
  //   return (
  //     <el-input v-model="text" v-bind="$attrs" v-on="$listeners" v-on:input.native="changeValue">
  //       {slots}
  //     </el-input>);
  // }
};
</script>

